@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@100;500&family=Inter:wght@200;600&family=Open+Sans:wght@300&display=swap");
:root {
  --limegreen: #1db489;
  --brightred: #dc414c;
  --facebook: #198ff5;
  --twitter: #1ca0f2;
  --instagram-start: #fdc468;
  --instagram-middle: #ee867c;
  --instagram-end: #df4996;
  --youtube: #c4032a;
  --toggle-bg-light: #aeb3cb;
  --toggle-bg-start: #378fe6;
  --toggle-bg-end: #3eda82;
  --toggle-light: #868cac;
  --toggle-button-light: #f0f2f9;
  --dark-bg: #1e202a;
  --dark-top-bg: #1f212e;
  --dark-card: #252a41;
  --dark-card-hover: #343b56;
  --dark-text1: #8b97c6;
  --dark-text2: #fff;
  --light-bg: #fff;
  --light-top-bg: #f5f7ff;
  --light-card: #f0f2fa;
  --light-card-hover: #e0e4f0;
  --light-text1: #63677e;
  --light-text2: #1e202a;
  --background: var(--light-bg);
  --text-color2:var(--light-text2);
  --text-color1:var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

* {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  background: var(--background);
  color: var(--text-color);
  margin: 0;
  padding: 0;
}

body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

.heading {
  display: flex;
  flex-wrap: wrap;
  margin-top: 3.125rem;
  align-items: center;
  justify-content: space-between;
}

.heading__toggle {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.toggle-sub {
  color: var(--toggle);
  font-weight: 800;
  font-size: 0.9375rem;
  padding-right: 0.3125rem;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.container {
  margin: 0 auto 2.5rem;
  max-width: 75rem;
}

h1 {
  font-size: 1.5625rem;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 1.5rem;
  color: var(--text-color1);
}

h1, h2, p {
  font-weight: bolder;
  line-height: 1.1;
}

a, a:visited, a:active {
  text-decoration: none;
}

p {
  font-size: 0.9375rem;
  color: var(--text-color1);
}

.main {
  margin: 1.25rem auto;
}

.footer {
  text-align: center;
  font-size: 0.75rem;
  margin-bottom: 0.625rem;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.25rem;
}

.card {
  position: relative;
  background: var(--card-bg);
  border-radius: 0.3125rem;
  color: var(--dark-text1);
  padding: 1.25rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
  overflow: hidden;
}
.card:hover {
  background: var(--card-hover);
}
.card__title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1.25rem 0;
}
.card__icon {
  font-size: 3rem;
  margin-right: 0.3125rem;
}
.card__name {
  font-size: 0.75rem;
  font-weight: bold;
  color: var(--text-color1);
}
.card__follower {
  margin-bottom: 0.9375rem;
}
.card__count--big {
  font-size: 3.125rem;
  color: var(--text-color2);
  font-weight: 900;
}
.card__count--small {
  font-size: 1.875rem;
  color: var(--text-color2);
  font-weight: 900;
}
.card__label {
  color: var(--text-color2);
  font-weight: 300;
  letter-spacing: 0.375rem;
}
.card__update {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.9375rem 0;
  font-size: 0.9375rem;
  color: var(--limegreen);
}
.card__update--down {
  color: var(--brightred);
}
.card__number {
  margin-left: 0.3125rem;
  font-size: 0.75rem;
  font-weight: 600;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 0.3125rem;
  top: 0;
  left: 0;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}

.card-item {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
}

.card__update, img {
  justify-self: end;
}

.card-item .card__update {
  margin: 0;
}

.card__count, .card-item .card__update {
  align-self: end;
}

.card__sub {
  font-weight: 600;
  color: var(--text-color1);
}